// app/preview/[category]/[id]/page.tsx
"use client";
import { notFound, useParams } from "next/navigation";
import ImageGallery from "./ImageGallery";
import { useGetPostDetail } from "@/app/lib/postServices/postQueries";

export default function PostPreviewPage() {
  const params = useParams();
  const { category, id } = params;

  let isCar = false;
  let isApartment = false;
  let isHouse = false;
  let isMobile = false;
  let isElec = false;
  let isOutdoor = false;
  let cat: string = "generic";
  if (category === "cars") {
    isCar = true;
    cat = "cars";
  } else if (category === "mobiles") {
    isMobile = true;
    cat = "mobile"; // استخدام "mobile" ليتوافق مع الـ API
  } else if (category === "apartments") {
    isApartment = true;
    cat = "apartments";
  } else if (category === "houses") {
    isHouse = true;
    cat = "houses";
  } else if (category === "electronics") {
    isElec = true;
    cat = "electronics";
  } else if (category === "outdoor-space") {
    isOutdoor = true;
    cat = "outdoor-space";
  }

  console.log("id  ", id);
  console.log("cat  ", cat);
  const { data, isLoading, error } = useGetPostDetail(cat, id);

  // التعامل مع حالات التحميل والأخطاء
  if (isLoading) return <div>جاري التحميل...</div>;
  if (error) return <div>خطأ: {error.message}</div>;
  if (!data) return notFound();

  const allImages = [
    { id: 0, image: data.cover_image },
    ...(data.gallery_images || []),
  ];

  return (
    <main className="grid md:grid-cols-2 gap-6 p-4">
      <ImageGallery images={allImages} />
      <div className="space-y-4">
        <h1 className="text-2xl font-bold">{data.title}</h1>
        <p className="text-cgreen font-bold text-xl">{data.price} ليرة سورية</p>
        <p className="text-gray-600">{data.description}</p>

        {isMobile && data.mobile && (
          <div>
            <h2 className="text-lg font-semibold text-cgreen border-b pb-1 mb-2">
              تفاصيل المنتج
            </h2>
            <ul className="text-sm text-gray-700 space-y-1">
              {data.mobile.brand && (
                <li>
                  <strong>العلامة التجارية: </strong>
                  {data.mobile.brand}
                </li>
              )}
              {data.mobile.color && (
                <li>
                  <strong>اللون: </strong>
                  {data.mobile.color}
                </li>
              )}
              {data.mobile.accessories !== undefined && (
                <li>
                  <strong>الإكسسوارات: </strong>
                  {data.mobile.accessories ? "نعم" : "لا"}
                </li>
              )}
            </ul>
          </div>
        )}

        <button className="w-full bg-cgreen text-white py-2 rounded hover:bg-chgreen transition">
          تواصل مع البائع
        </button>
      </div>
    </main>
  );
}
